  --color-lightyellow: #c9c9a0;
  --color-warningred: #ff6262;
  --color-confirmationgreen: #00ff00;
  --color-disabledgray: #bbbbbb;
  --color-black: #000000;
  --color-white: #ffffff;
  --color-box: red;

  --color-text-main: #f2ff61;

  /*SIZE*/
  ---main-padding: 0px;

  ---border-radius: 0px;
  ---border-radius-button: 0px;
  ---border-radius-small: 0px;

  ---navbar-height: 100px;

  /* E Additions*/
