.SlideLeftAnimation {
     opacity: 1;
     -webkit-animation: slideLeft 0.8s ease-in-out forwards;
             animation: slideLeft 0.8s ease-in-out forwards;
}

.SlideRightAnimation {
     opacity: 1;
     -webkit-animation: slideRight 0.8s ease-in-out forwards;
             animation: slideRight 0.8s ease-in-out forwards;
}

.SlideUpAnimation {
     opacity: 1;
     -webkit-animation: slideUp 0.8s ease-in-out forwards;
             animation: slideUp 0.8s ease-in-out forwards;
}

.SlideDownAnimation {
     opacity: 1;
     -webkit-animation: slideDown 0.8s ease-in-out forwards;
             animation: slideDown 0.8s ease-in-out forwards;
}

@-webkit-keyframes slideLeft {
     0% {
          -webkit-transform: translateX(-300px);
                  transform: translateX(-300px);
          opacity: 0;
     }
     100% {
          -webkit-transform: translateX(0px);
                  transform: translateX(0px);
          opacity: 1;
     }
}

@keyframes slideLeft {
     0% {
          -webkit-transform: translateX(-300px);
                  transform: translateX(-300px);
          opacity: 0;
     }
     100% {
          -webkit-transform: translateX(0px);
                  transform: translateX(0px);
          opacity: 1;
     }
}

@-webkit-keyframes slideRight {
     0% {
          -webkit-transform: translateX(300px);
                  transform: translateX(300px);
          opacity: 0;
     }
     100% {
          -webkit-transform: translateX(0px);
                  transform: translateX(0px);
          opacity: 1;
     }
}

@keyframes slideRight {
     0% {
          -webkit-transform: translateX(300px);
                  transform: translateX(300px);
          opacity: 0;
     }
     100% {
          -webkit-transform: translateX(0px);
                  transform: translateX(0px);
          opacity: 1;
     }
}

@-webkit-keyframes slideUp {
     0% {
          -webkit-transform: translateY(300px);
                  transform: translateY(300px);
          opacity: 0;
     }
     100% {
          -webkit-transform: translateY(0px);
                  transform: translateY(0px);
          opacity: 1;
     }
}

@keyframes slideUp {
     0% {
          -webkit-transform: translateY(300px);
                  transform: translateY(300px);
          opacity: 0;
     }
     100% {
          -webkit-transform: translateY(0px);
                  transform: translateY(0px);
          opacity: 1;
     }
}

@-webkit-keyframes slideDown {
     0% {
          -webkit-transform: translateY(-300px);
                  transform: translateY(-300px);
          opacity: 0;
     }
     100% {
          -webkit-transform: translateY(0px);
                  transform: translateY(0px);
          opacity: 1;
     }
}

@keyframes slideDown {
     0% {
          -webkit-transform: translateY(-300px);
                  transform: translateY(-300px);
          opacity: 0;
     }
     100% {
          -webkit-transform: translateY(0px);
                  transform: translateY(0px);
          opacity: 1;
     }
}

